.review {
  width: 320px;
  margin-top: 36px;
  margin-bottom: 16px;

  @media (min-width: $tablet-width) {
    width: 768px;
    margin-top: 63px;
  }

  @media (min-width: $desktop-width) {
    width: 1200px;
    margin-top: 115px;
  }
}

.review__title {
  margin: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 30px;
  }
}

.review__text {
  @media (min-width: $mobile-width) {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    text-transform: uppercase;
    margin: 0;
    margin-top: 30px;
  }

  @media (min-width: $tablet-width) {
    margin-top: 33px;
  }
}
